<template>
  <div>
    <my-header></my-header>
    <el-main>
      <div id="collectionListPage" class="clearFixed">
        <el-card class="errList">
          <el-table v-loading="loading" :data="tableData" style="width: 100%;height:460px">
            <el-table-column prop="code" label="" width="180"></el-table-column>
            <el-table-column prop="deptname" label="" width="180"></el-table-column>
            <el-table-column prop="typeName" label="" width="180"></el-table-column>
            <el-table-column prop="weight" label="" width="180"></el-table-column>
            <el-table-column prop="time" label="" width="180"></el-table-column>
            <el-table-column prop="person" label="" width="180"></el-table-column>
            <el-table-column fixed="right" label="">
              <template slot-scope="scope">
                <el-button v-on:click="print(scope.row)" type="text" size="small"></el-button>
                <el-button v-on:click="del(scope.row,scope.$index)" type="text" size="small"></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-card>
      </div>
    </el-main>
    <my-footer :active="active"></my-footer>
  </div>
</template>

<script>
import myHeader from './header';
import myFooter from './footer';
import {_Widget} from "../common";
import {Downloader} from "../common";
import {printLable} from '../../static/mmprinter.js'
export default {
  name: 'collectionList',
  components:{
    myHeader,
    myFooter,
  },
  data: () => ({
    active:'/error',
    car: '',
    doctor:'',
    loading: false,//
    seltype:0,//
    selErrtype:0,//
    tableData: [],

  }),
  methods: {
    printLable,
    selType: function (index,errtype) {
      this.seltype = index;
      this.selErrtype = errtype;
      this.getData(this.selErrtype)
    },
    goto(url){
      this.$router.push(url)
    },
    handleClick(res){

    },
    getdeptData(){
      var thisObject = this;
      var url = thisObject.config.baseURL + "of_cj_sy";
      url += "?hospitalCode=" + thisObject.config.hospitalCode + "&depcode=" + thisObject.deptid;
      Downloader.loadXMLAsync(url, function(e) {
        var xmldoc = e.response;
        var obj = {};
        var xml = _Widget.xml.jsonToxml(xmldoc);
        var json = _Widget.xml.xmlToJson(xml);
        var res = (JSON.parse(json.string["#text"]));
        if(res.result == 1){
          res.data.forEach(function (v){
            switch (v.type){
              case 1:
                v.typeName = "";
                break;
              case 2:
                v.typeName = "";
                break;
              case 3:
                v.typeName = "";
                break;
              case 4:
                v.typeName = "";
                break;
              case 5:
                v.typeName = "";
                break;
              default:
            }
          })
          thisObject.tableData = res.data;
        }
      },function (err) {
        thisObject.$message({
          type: 'error',
          message: '!'
        });
        thisObject.tableData =[];
      })
    },
    print(data){
      var params = {
        deptname: "" + data.deptname,
        time: "" + data.time,
        type: data.typeName,
        weight: "" + data.weight,
        hospital: "" + this.config.hospitalName,
        code: data.code,
        person: '' + data.person
      };
      this.printLable(params)
    },
    del(data,index){
      var thisObject = this;
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        var time = new Date().getTime();
        var t = _Widget.dateUtil.formatDateTime2(time,'yyyy-MM-dd HH:mm:ss');
        var id = thisObject.operatorId;//
        var url = thisObject.config.baseURL + "of_yf_tj";
        url += "?hospitalCode=" + thisObject.config.hospitalCode;
        var linshiData = {
          type: "06",
          code: data.code,
          id:id,
          time:t
        };
        Downloader.loadXMLAsync(url + "&as_json=" + JSON.stringify(linshiData),function(e){
          var xmldoc = e.response;
          var xml = _Widget.xml.jsonToxml(xmldoc);
          var json = _Widget.xml.xmlToJson(xml);
          var res = (JSON.parse(json.string["#text"]));
          if(res.result == 1){
            thisObject.tableData.splice(index, 1);
            thisObject.$message({
              type: 'success',
              message: '!'
            });
          }
        },function (err) {
          thisObject.$message({
            type: 'error',
            message: '!'
          });
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });

    }
  },
  mounted(){
    var thisObject = this;
    this.config = JSON.parse(localStorage.getItem("config"));
    this.operator = localStorage.getItem("operator");
    this.operatorId = localStorage.getItem("operatorId");
    this.deptid = this.$route.query.deptid;
    this.getdeptData();
  }
}
</script>



