<template>
  <footer class="footer">
    <ul>
      <li v-for="nav in navs" :class="{on:$route.name==nav.name}" v-on:click="goto(nav.href)">{{nav.name}}</li>
    </ul>
  </footer>
</template>
<script>
export default {
  name: 'myFooter',

  data:() => ({
    isSelect:'/',
    navs:[{
      href:"/",
      name:""
    },{
      href:"/updata",
      name:""
    },{
      href:"/intoRoom",
      name:""
    },{
      href:"/outRoom",
      name:""
    },{
      href:"/error",
      name:""
    }]
  }),
  methods:{
    goto(url){
      this.$router.push(url)
    }
  },
  created: function () {
    this.isSelect = this.$route.path;
  }
};
</script>





