<template>
  <div>
    <my-header></my-header>
    <el-main>
      <div id="updataDetailPage" class="clearFixed">
        <el-card class="errList">
          <el-table v-loading="loading" :data="tableData" style="width: 100%;height:460px">
            <el-table-column prop="index" label="" width="60"></el-table-column>
            <el-table-column prop="num" label="" width="180"></el-table-column>
            <el-table-column prop="address" label="" width="180"></el-table-column>
            <el-table-column prop="type" label="" width="180"></el-table-column>
            <el-table-column prop="weight" label="" width="100"></el-table-column>
            <el-table-column prop="unit" label="" width="100"></el-table-column>
            <el-table-column prop="date" label="" width="180"></el-table-column>
            <el-table-column fixed="right" label="">
              <template slot-scope="scope">
                <el-button v-on:click="deletes('')" type="text" size="small"></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-card>
      </div>
    </el-main>
    <my-footer :active="active"></my-footer>
  </div>
</template>

<script>
import myHeader from './header';
import myFooter from './footer';
import {baseURL} from '../common.js';
import  axios from 'axios';
export default {
  name: 'updataDetail',
  components:{
    myHeader,
    myFooter,
  },
  data: () => ({
    active:'/error',
    car: '',
    doctor:'',
    loading: false,//
    seltype:0,//
    selErrtype:0,//
    tableData: [],

  }),
  methods: {
    selType: function (index,errtype) {
      this.seltype = index;
      this.selErrtype = errtype;
      this.getData(this.selErrtype)
    },
    goto(url){
      this.$router.push(url)
    },
    handleClick(res){

    },
    getData(seltype){
      var url = baseURL + "targetIDToPoiID?bdid=B000A11DDU&targetID=" + seltype
      axios.get(url)
        .then(function (res) {
          console.log(res);
          if(res.data.result == 1){
            this.$data.tableData = res.data;
          }
        })
        .catch(function (error) {
          console.log(error);
        })
    },
    deletes() {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.$message({
          type: 'success',
          message: '!'
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
  },
  mounted(){
    this.config = JSON.parse(localStorage.getItem("config"));
  }
}
</script>



